<template>
  <div class="login-page">
    <!--login-left start-->
    <div class="login-left">
      <div class="login-left-con">
        <div class="login-logo"><img src="../assets/images/login2.png"></div>
        <div class="login-en">Welcome</div>
        <div class="login-ch">欢迎登陆机满满管理后台</div>
      </div>
    </div>
    <!--login-left end-->
    <!--login-right start-->
    <div class="login-right">
      <div class="login-right-con">
        <div class="login-tit">登录</div>
        <div class="login-ul">
          <div class="login-item"><input type="text" class="login-input" placeholder="请输入账号/用户名" /> </div>
          <div class="login-item"><input type="text" class="login-input" placeholder="请输入密码" /> </div>
        </div>
        <div class="login-forget">
          <router-link class="login-forget-a" to="/password">忘记密码</router-link>
        </div>
        <div class="login-bot">
          <button class="login-btn" @click="gotolink">登录</button>
        </div>
      </div>
    </div>
    <!--login-right end-->
  </div>
</template>

<script>
export default {
  name: "login",
  //跳转页面
  methods:{
    //跳转页面
    gotolink(){
      this.$router.push({path:'/personal'})
    }
  }

}
</script>

<style scoped>
@import "../assets/styles/login.css";
</style>



